import React, { useContext } from 'react'
import StatsContext from './../../../context/thesportsdb/statsContext'
import FormerClubs from './FormerClubs'

const PlayerGrid = ({ player }) => {
    const statsContext = useContext(StatsContext);
    const { formerClubs } = statsContext;

    return (
        <div className="infos">
            <div className="main-part">
                <div className="image">
                    <img
                        src={player.strCutout ? player.strCutout : 'https://cdn4.iconfinder.com/data/icons/football-4/100/jersey_3-512.png'}
                        alt=""
                    />
                </div>

                <div className="desc">
                    <h1>{player.strPlayer}</h1>
                    <p>Nationatlity: <span>{player.strNationality}</span></p>
                    <p>Date of birth: <span>{player.dateBorn}</span></p>
                    <p>Date of debut: <span>{player.dateSigned}</span></p>
                    <p>Birth location: <span>{player.strBirthLocation}</span></p>
                    <p>Team: <span>{player.strTeam}</span></p>
                    <p>Position: <span>{player.strPosition}</span></p>
                    <p>Height: <span>{player.strHeight}</span></p>
                    <p>Weight: <span>{`${player.strWeight} kg`}</span></p>
                    <p className="main-desc">{player.strDescriptionEN}</p>
                    <div className="social-links">
                        <a href="#"><i className="fab fa-facebook fb"></i></a>
                        <a href="#"><i className="fab fa-instagram ig"></i></a>
                        <a href="#"><i className="fab fa-twitter tw"></i></a>
                    </div>
                    <div className="past-clubs">
                        <h1>Former teams</h1>
                        {formerClubs && formerClubs.map(formerClub => <FormerClubs key={formerClub.idFormerTeam} formerClub={formerClub} />)}

                    </div>
                </div>
            </div>
        </div>
    )
}

export default PlayerGrid
