import React, { Fragment, useContext } from 'react'
import Event from './Event'
import PastEvent from './PastEvent'
import StatsContext from './../../context/thesportsdb/statsContext'

const Events = () => {
    const statsContext = useContext(StatsContext);
    const { events, pastEvents } = statsContext;
    return (
        <Fragment>
            <h1>Today's events</h1>
            <div className="events">
                {events.map((event, index) => index < 8 ? <Event key={event.idEvent} event={event} /> : '')}
                {/* <div className="event">
                    <p>Houston Astros vs Tampa Bay Rays</p>
                    <p className="small">Starts at: 21:40</p>
                </div> */}
            </div>
            <h1>Last year on this day</h1>
            <div className="events">
                {pastEvents.map((event, index) => index < 5 ? <PastEvent key={event.idEvent} event={event} /> : '')}

            </div>
        </Fragment>
    )
}

export default Events
