import React from 'react'

const LeagueInfo = ({ info }) => {

    return (
        <div className="infos">
            <div className="main-part">
                <div className="image">
                    <img
                        src={info.strLogo ? info.strLogo : 'http://pngimg.com/uploads/football/football_PNG52789.png'}
                        alt=""
                    />
                </div>

                <div className="desc">
                    <h1>{info.strLeague}</h1>
                    <p>Country: <span>{info.strCountry}</span></p>
                    <p>Formed year: <span>{info.intFormedYear}</span></p>

                    <p className="main-desc">{info.strDescriptionEN}</p>
                    <div className="social-links">
                        <a href={info.strFacebook}><i className="fab fa-facebook fb"></i></a>
                        <a href={info.strTwitter}><i className="fab fa-twitter tw"></i></a>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default LeagueInfo
