import React, { Fragment, useContext, useEffect } from 'react'
import HomeNews from './HomeNews'
import MainArticle from './HomeMainArticle'
import Events from './../Events'
import Navbar from './../Navbar'
import Footer from './../Footer'
import NewsapiContext from './../../../context/newsapi/newsapiContext';
import StatsContext from './../../../context/thesportsdb/statsContext';
import uuid from 'uuid';

const Home = () => {
    const newsapiContext = useContext(NewsapiContext);
    const { searchNews, news } = newsapiContext;

    const statsContext = useContext(StatsContext);
    const { setTodaysEvents, setPastEvents } = statsContext;

    useEffect(() => {
        let newDate = new Date()
        let date = newDate.getDate();
        let month = newDate.getMonth() + 1;
        let year = newDate.getFullYear();
        let search = `${year}-${month}-${date}`;
        let search2 = `${year - 1}-${month}-${date}`;
        searchNews();
        setTodaysEvents(search);
        setPastEvents(search2);

    }, [])



    return (
        <Fragment>
            <Navbar />
            <div className="container">
                <section className="section-main">
                    <div className="section-main-left">
                        <div className="section-main-left-newsGridFlex">
                            {news.map((article, index) => index === 0 ? <MainArticle key={uuid.v4()} article={article} /> : (index < 10 ? <HomeNews key={uuid.v4()} article={article} /> : ''))}

                        </div>
                    </div>
                    <div className="section-main-right">
                        <Events />
                    </div>
                </section>
            </div>
            <Footer />
        </Fragment>
    )
}

export default Home
