import React, { useContext } from 'react'
import NewsapiContext from './../../../context/newsapi/newsapiContext';
import { Link } from 'react-router-dom';

const HomeNews = ({ article }, props) => {
    const newsapiContext = useContext(NewsapiContext);
    const { setCurrentArticle } = newsapiContext;
    const onClick = (e) => {

        setCurrentArticle(article.title);
        // props.history.push(`/news/${article.title}`)
    }

    return (
        <Link to={`/news/${article.title}`} className="article" onClick={onClick}>
            <div className="article-img" >
                <img
                    src={article.urlToImage ? article.urlToImage : 'http://newsboss.in/img/3/1.jpg'}
                    alt=""
                />
            </div>
            <div className="article-desc">
                <div className="article-desc-heading">
                    <h1>
                        {article.title}
                    </h1>
                </div>
            </div>
        </Link>
    )
}

export default HomeNews
