import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import StatsContext from './../../../context/thesportsdb/statsContext';
const FootballGrid = ({ league }) => {
    const statsContext = useContext(StatsContext);
    const { currentLeague, getLeagueByID } = statsContext;
    const onClick = () => {
        getLeagueByID(league.idLeague);
    }
    return (
        <Link to={`/league/${league.idLeague}`} className="card" id={league.idLeague} onClick={onClick}>
            <div className="image">
                <img
                    src='http://pngimg.com/uploads/football/football_PNG52789.png'
                    alt=""
                />
            </div>
            <h1>{league.strLeague}</h1>
        </Link>
    )
}

export default FootballGrid
