import {
    SEARCH_NEWS,
    SET_CURRENT_ARTICLE

} from '../types';

export default (state, action) => {
    switch (action.type) {
        case SEARCH_NEWS:
            return {
                ...state,
                news: action.payload
            }
        case SET_CURRENT_ARTICLE:
            return {
                ...state,
                currentArticle: state.news.find(article => article.title === action.payload)
            }
        default: return state;
    }
}