import React, { Fragment, useContext } from 'react'
import Events from './../Events'
import Navbar from './../Navbar'
import Footer from './../Footer'
import PlayerGrid from './PlayerGrid'
import StatsContext from './../../../context/thesportsdb/statsContext'

const Player = () => {
    const statsContext = useContext(StatsContext);
    const { player, formerClubs } = statsContext;

    return (
        <Fragment>
            <Navbar />
            <div className="container">
                <section className="section-main">
                    <div className="section-main-left">
                        {(player && formerClubs) && <PlayerGrid key={player.idPlayer} player={player} />}
                    </div>
                    <div className="section-main-right">
                        <Events />
                    </div>
                </section>
            </div>
            <Footer />
        </Fragment>
    )
}

export default Player
