import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import Home from './components/pages/Home/Home';
import Article from './components/pages/Article/Article';
import News from './components/pages/News/News';
import Football from './components/pages/Football/Football';
import League from './components/pages/League/League';
import Club from './components/pages/Club/Club';
import Player from './components/pages/Player/Player';

import './App.css';
import NewsapiState from './context/newsapi/NewsapiState';
import StatsState from './context/thesportsdb/StatsState';

function App() {
  return (
    <StatsState>
      <NewsapiState>
        <Router basename='/'>
          <div className="App">
            <Switch>
              <Route exact path='/' component={Home} />
              <Route exact path='/news/:name' component={Article} />
              <Route exact path='/news' component={News} />
              <Route exact path='/football' component={Football} />
              <Route exact path='/league/:name' component={League} />
              <Route exact path='/club/:name' component={Club} />
              <Route exact path='/player/:name' component={Player} />
            </Switch>
          </div>
        </Router>
      </NewsapiState>
    </StatsState>
  );
}

export default App;
