import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import StatsContext from './../../../context/thesportsdb/statsContext'

const LeagueGrid = ({ club }) => {
    const statsContext = useContext(StatsContext);
    const { setCurrentClub, getLast5Events, getNext5Events, getRoster } = statsContext;


    const onClick = () => {
        setCurrentClub(club.idTeam);
        getNext5Events(club.idTeam);
        getLast5Events(club.idTeam);
        getRoster(club.idTeam);
    }
    return (
        <Link to={`/club/${club.idTeam}`} className="card" onClick={onClick}>
            <div className="image">
                <img
                    src={club.strTeamBadge}
                    alt=""
                />
            </div>
            <h1>{club.strTeam}</h1>
        </Link>
    )
}

export default LeagueGrid
