import React, { useContext, useEffect } from 'react'
import Result from './Result'
import Upcoming from './Upcoming'
import StatsContext from './../../../context/thesportsdb/statsContext';
import Roster from './Roster'

const ClubGrid = ({ currentClub }) => {
    const statsContext = useContext(StatsContext);
    const { last5Events, next5Events, roster } = statsContext;

    return (
        <div className="infos">
            <div className="main-part">
                <div className="image">
                    <img
                        src={currentClub.strTeamBadge}
                        alt=""
                    />
                </div>

                <div className="desc">
                    <h1>{currentClub.strTeam} <span>{`(${currentClub.strCountry})`}</span></h1>
                    <p>League: <span>{currentClub.strLeague}</span></p>
                    <p>Formed year: <span>{currentClub.intFormedYear}</span></p>
                    <p>Stadium: <span>{currentClub.strStadium}</span></p>
                    <p>Stadium capacity: <span>{currentClub.intStadiumCapacity}</span></p>
                    <p className="main-desc">
                        {currentClub.strDescriptionEN}
                    </p>
                    <div className="social-links">
                        <a href="#"><i className="fab fa-facebook fb"></i></a>
                        <a href="#"><i className="fab fa-instagram ig"></i></a>
                        <a href="#"><i className="fab fa-twitter tw"></i></a>
                    </div>
                </div>
            </div>
            <div className="schedule">
                <h1>Schedule</h1>
                <div className="sides">
                    <div className="last-5">
                        <h1>Last 5 matches</h1>
                        {last5Events && last5Events.map(event => <Result key={event.idEvent} event={event} />)}

                    </div>
                    <div className="next-5">
                        <h1>Next 5 matches</h1>
                        {next5Events && next5Events.map(e => <Upcoming key={e.idEvent} event={e} />)}
                    </div>
                </div>
            </div>
            <div className="roster">
                <h1>Team roster</h1>
                <div className="grid">
                    {roster && roster.map(player => <Roster key={player.idPlayer} player={player} />)}

                </div>
            </div>
        </div>
    )
}

export default ClubGrid
