import React, { useContext } from 'react'
import NewsapiContext from './../../../context/newsapi/newsapiContext';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
const NewsGrid = ({ article }) => {
    const newsapiContext = useContext(NewsapiContext);
    const { setCurrentArticle } = newsapiContext;

    const onClick = (e) => {

        setCurrentArticle(e.target.textContent);
    }
    const clickImage = (e) => {
        setCurrentArticle(e.target.parentElement.nextSibling.firstElementChild.textContent);
    }

    return (
        <div className="articles-grid-news" >
            <div className="art" >
                <div className="image" onClick={clickImage}>
                    <img
                        src={article.urlToImage ? article.urlToImage : 'http://newsboss.in/img/3/1.jpg'}
                        alt=""

                    />
                </div>
                <div className="desc">
                    <Link to={`/news/${article.title}`} style={myStyle}>
                        <h1 onClick={onClick}>
                            {article.title}
                        </h1>
                    </Link>
                    <p>
                        {article.description}
                    </p>

                </div>
            </div>
        </div>
    )
}

const myStyle = {
    textDecoration: 'none'
}
export default withRouter(NewsGrid)
