import React, { useReducer } from 'react';
import axios from 'axios';
import NewsapiContext from './newsapiContext';
import NewsapiReducer from './newsapiReducer';

import {
    SEARCH_NEWS,
    SET_CURRENT_ARTICLE
} from '../types';

const NewsapiState = props => {
    const initialState = {
        news: [],
        currentArticle: null
    }

    const [state, dispatch] = useReducer(NewsapiReducer, initialState);

    const searchNews = async () => {
        const res = await axios.get('https://newsapi.org/v2/everything?domains=espn.com&apiKey=4ab1044088284c12b01219057a75cfd7')
        dispatch({
            type: SEARCH_NEWS,
            payload: res.data.articles
        })
    }

    const setCurrentArticle = (title) => {
        dispatch({
            type: SET_CURRENT_ARTICLE,
            payload: title
        })
    }


    return <NewsapiContext.Provider
        value={{
            news: state.news,
            searchNews,
            currentArticle: state.currentArticle,
            setCurrentArticle

        }}
    >
        {props.children}
    </NewsapiContext.Provider>
}

export default NewsapiState;