import React, { Fragment, useContext } from 'react'
import ArticleGrid from './ArticleGrid'
import Navbar from './../Navbar'
import Events from './../Events'
import Footer from './../Footer'
import NewsapiContext from './../../../context/newsapi/newsapiContext'

const Article = () => {
    const newsapiContext = useContext(NewsapiContext);
    const { currentArticle } = newsapiContext;

    return (
        <Fragment>
            <Navbar />
            <div className="container">
                <section className="section-main">
                    <div className="section-main-left">
                        <ArticleGrid />
                    </div>
                    <div className="section-main-right">
                        <Events />
                    </div>
                </section>
            </div>
            <Footer />
        </Fragment>
    )
}

export default Article
