import React, { Fragment, useContext, useEffect } from 'react'
import Navbar from './../Navbar'
import Events from './../Events'
import Footer from './../Footer'
import LeagueGrid from './LeagueGrid'
import LeagueInfo from './LeagueInfo'
import StatsContext from './../../../context/thesportsdb/statsContext';

const League = () => {
    const statsContext = useContext(StatsContext);
    const { getAllClubsInLeague, clubs, currentLeague } = statsContext;


    useEffect(() => {
        var pageURL = window.location.href;
        var lastURLSegment = pageURL.substr(pageURL.lastIndexOf('/') + 1);
        getAllClubsInLeague(lastURLSegment);
    }, [])

    return (
        <Fragment>
            <Navbar />
            <div className="container">
                <section className="section-main">
                    <div className="section-main-left">
                        {currentLeague && <LeagueInfo key={currentLeague.idLeague} info={currentLeague} />}
                        <div className="cards">
                            {clubs !== null ? clubs.map(club => <LeagueGrid key={club.idTeam} club={club} />) : 'Api Error'}
                        </div>
                    </div>
                    <div className="section-main-right">
                        <Events />
                    </div>
                </section>
            </div>
            <Footer />
        </Fragment>
    )
}

export default League
