import React, { useReducer } from 'react';
import axios from 'axios';
import StatsContext from './statsContext';
import StatsReducer from './statsReducer';

import {
    SET_TODAYS_EVENTS,
    SET_LAST_YEAR_EVENTS,
    GET_ALL_LEAGUES,
    GET_LEAGUE_BY_ID,
    GET_ALL_CLUBS_IN_LEAGUE_BY_ID,
    SET_CURRENT_CLUB,
    GET_LAST_5_EVENTS,
    GET_NEXT_5_EVENTS,
    GET_ROSTER,
    SET_PLAYER,
    SET_FORMER_CLUBS
} from '../types';

const NewsapiState = props => {
    const initialState = {
        events: [],
        pastEvents: [],
        leagues: [],
        currentLeague: null,
        clubs: [],
        currentClub: null,
        last5Events: null,
        next5Events: null,
        roster: null,
        player: null,
        formerClubs: null
    }

    const [state, dispatch] = useReducer(StatsReducer, initialState);


    const setTodaysEvents = async (search) => {


        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/eventsday.php?d=${search}`);

        dispatch({
            type: SET_TODAYS_EVENTS,
            payload: res.data.events
        })
    }
    const setPastEvents = async (search) => {


        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/eventsday.php?d=${search}`);

        dispatch({
            type: SET_LAST_YEAR_EVENTS,
            payload: res.data.events
        })
    }

    const getLeagues = async () => {

        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/all_leagues.php`);

        dispatch({
            type: GET_ALL_LEAGUES,
            payload: res.data.leagues
        })
    }

    const getLeagueByID = async (ID) => {

        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/lookupleague.php?id=${ID}`);
        dispatch({
            type: GET_LEAGUE_BY_ID,
            payload: res.data.leagues[0]
        })
    }

    const getAllClubsInLeague = async (ID) => {

        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/lookup_all_teams.php?id=${ID}`);
        dispatch({
            type: GET_ALL_CLUBS_IN_LEAGUE_BY_ID,
            payload: res.data.teams
        })
    }

    const setCurrentClub = async (ID) => {

        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/lookupteam.php?id=${ID}`);

        dispatch({
            type: SET_CURRENT_CLUB,
            payload: res.data.teams[0]
        })
    }

    const getLast5Events = async (ID) => {

        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/eventslast.php?id=${ID}`);

        dispatch({
            type: GET_LAST_5_EVENTS,
            payload: res.data.results
        })
    }

    const getNext5Events = async (ID) => {

        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/eventsnext.php?id=${ID}`);

        dispatch({
            type: GET_NEXT_5_EVENTS,
            payload: res.data.events
        })
    }

    const getRoster = async (ID) => {

        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/lookup_all_players.php?id=${ID}`);

        dispatch({
            type: GET_ROSTER,
            payload: res.data.player
        })
    }


    const setPlayer = async (ID) => {

        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/lookupplayer.php?id=${ID}`);

        dispatch({
            type: SET_PLAYER,
            payload: res.data.players[0]
        })
    }

    const setFormerClubs = async (ID) => {

        const res = await axios.get(`https://www.thesportsdb.com/api/v1/json/1/lookupformerteams.php?id=${ID}`);

        dispatch({
            type: SET_FORMER_CLUBS,
            payload: res.data.formerteams
        })
    }

    return <StatsContext.Provider
        value={{
            events: state.events,
            setTodaysEvents,
            pastEvents: state.pastEvents,
            setPastEvents,
            leagues: state.leagues,
            getLeagues,
            currentLeague: state.currentLeague,
            getLeagueByID,
            clubs: state.clubs,
            getAllClubsInLeague,
            currentClub: state.currentClub,
            setCurrentClub,
            last5Events: state.last5Events,
            getLast5Events,
            next5Events: state.next5Events,
            getNext5Events,
            roster: state.roster,
            getRoster,
            player: state.player,
            setPlayer,
            formerClubs: state.formerClubs,
            setFormerClubs

        }}
    >
        {props.children}
    </StatsContext.Provider>
}

export default NewsapiState;