import {
    SET_TODAYS_EVENTS,
    SET_LAST_YEAR_EVENTS,
    GET_ALL_LEAGUES,
    GET_LEAGUE_BY_ID,
    GET_ALL_CLUBS_IN_LEAGUE_BY_ID,
    SET_CURRENT_CLUB,
    GET_LAST_5_EVENTS,
    GET_NEXT_5_EVENTS,
    GET_ROSTER,
    SET_PLAYER,
    SET_FORMER_CLUBS

} from '../types';

export default (state, action) => {
    switch (action.type) {
        case SET_TODAYS_EVENTS:
            return {
                ...state,
                events: action.payload
            }
        case SET_LAST_YEAR_EVENTS:
            return {
                ...state,
                pastEvents: action.payload
            }
        case GET_ALL_LEAGUES:
            return {
                ...state,
                leagues: action.payload
            }
        case GET_LEAGUE_BY_ID:
            return {
                ...state,
                currentLeague: action.payload
            }
        case GET_ALL_CLUBS_IN_LEAGUE_BY_ID:
            return {
                ...state,
                clubs: action.payload
            }
        case SET_CURRENT_CLUB:
            return {
                ...state,
                currentClub: action.payload
            }
        case GET_LAST_5_EVENTS:
            return {
                ...state,
                last5Events: action.payload
            }
        case GET_NEXT_5_EVENTS:
            return {
                ...state,
                next5Events: action.payload
            }
        case GET_ROSTER:
            return {
                ...state,
                roster: action.payload
            }
        case SET_PLAYER:
            return {
                ...state,
                player: action.payload
            }
        case SET_FORMER_CLUBS:
            return {
                ...state,
                formerClubs: action.payload
            }
        default: return state;
    }
}