import React, { Fragment } from 'react'

const Footer = () => {
    return (
        <Fragment>
            <footer className="footer">
                <div className="container">
                    <div className="social-links">
                        <a href="#" className="twitter"><i className="fab fa-twitter"></i> Twitter</a>
                        <a href="#" className="fb"><i className="fab fa-facebook"></i> Facebook</a>
                        <a href="#" className="ig"><i className="fab fa-instagram"></i> Instagram</a>
                        <a href="#" className="yt"><i className="fab fa-youtube"></i> Youtube</a>
                    </div>
                    <div className="row">
                        <a href="#">Home</a>
                        <a href="#">News</a>
                        <a href="#">Football</a>
                        <a href="#">Basketball</a>
                        <a href="#">Formula</a>
                        <a href="#">Fighting</a>
                    </div>
                    <div className="poweredBy">
                        <p>
                            Powered By <a href="https://newsapi.org/">NewsAPI.org</a> and
            <a href="https://www.thesportsdb.com"> thesportsdb.com</a>
                        </p>
                    </div>
                    <div className="copyright">Copyright 2019 &copy; Faruk Obradović</div>
                </div>
            </footer>
        </Fragment>
    )
}

export default Footer
