import React, { useContext } from 'react'
import NewsapiContext from './../../../context/newsapi/newsapiContext';
import { Link } from 'react-router-dom';

const HomeMainArticle = ({ article }) => {
    const newsapiContext = useContext(NewsapiContext);
    const { setCurrentArticle } = newsapiContext;
    const onClick = (e) => {

        setCurrentArticle(article.title);
    }

    return (
        <Link to={`/news/${article.title}`} className="article-main" onClick={onClick}>
            <div className="article-main-img">
                <img
                    src={article.urlToImage ? article.urlToImage : 'http://newsboss.in/img/3/1.jpg'}
                    alt=""
                />
            </div>
            <div className="article-main-desc">
                <div className="article-main-desc-heading">
                    <h1>
                        {article.title}
                    </h1>
                </div>
                <div className="article-main-desc-p">
                    <p>
                        {article.description}
                    </p>
                </div>
            </div>
        </Link>
    )
}

export default HomeMainArticle
