import React, { useContext } from 'react'
import NewsapiContext from './../../../context/newsapi/newsapiContext'

const ArticleGrid = ({ article }) => {
    const newsapiContext = useContext(NewsapiContext);
    const { currentArticle } = newsapiContext;
    const activateNewTab = () => {
        window.open(currentArticle.url, '_blank');
    }
    return (
        <div className="article-news">
            <div className="source">{`Source: ${currentArticle.source.name}`}</div>
            <h1>
                {currentArticle.title}
            </h1>
            <div className="image">
                <img
                    src={currentArticle.urlToImage === null ? 'http://newsboss.in/img/3/1.jpg' : currentArticle.urlToImage}
                    alt=""
                />
            </div>
            <p className="description">
                {currentArticle.description}
            </p>
            <p className="content">
                {currentArticle.content}
            </p>
            <p onClick={activateNewTab} style={myStyle}>More info here </p>
            <p className="author"><i className="fas fa-user"></i> {currentArticle.author ? currentArticle.author : 'Unknown author'}</p>
        </div>
    )
}

const myStyle = {
    marginTop: '1rem',
    cursor: 'pointer',
    textDecoration: 'underline'
}

export default ArticleGrid
