import React from 'react'

const FormerClubs = ({ formerClub }) => {

    return (
        <div className="past-club">
            <div className="image">
                <img
                    src={formerClub.strTeamBadge}
                    alt=""
                />
            </div>
            <p style={myStyle}>{`${formerClub.strFormerTeam}  ${formerClub.strJoined}-${formerClub.strDeparted}`}</p>
        </div>
    )
}

const myStyle = {
    fontWeight: '400'
}
export default FormerClubs
