import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import StatsContext from './../../../context/thesportsdb/statsContext'

const Roster = ({ player }) => {
    const statsContext = useContext(StatsContext);
    const { setPlayer, setFormerClubs } = statsContext;

    const onClick = () => {
        setPlayer(player.idPlayer);
        setFormerClubs(player.idPlayer);
    }
    return (
        <Link to={`/player/${player.idPlayer}`} className="player" style={myStyle} onClick={onClick}>
            <div className="image">
                <img
                    src={player.strCutout ? player.strCutout : 'https://cdn4.iconfinder.com/data/icons/football-4/100/jersey_3-512.png'}
                    alt=""
                />
            </div>
            <p>{player.strPlayer}</p>
        </Link>
    )
}

const myStyle = {
    textDecoration: 'none',
    color: '#1e272e'
}
export default Roster
