import React from 'react'
import { NavLink } from 'react-router-dom'

const NavbarLinks = () => {
    return (
        <ul className="list">
            <li className="list-item" >
                <NavLink to='/' className="list-item-link" style={myStyle} activeClassName="active">Home</NavLink>
            </li>
            <li className="list-item">
                <NavLink to='/news' className="list-item-link" activeClassName="active">News</NavLink>
            </li>
            <li className="list-item">
                <NavLink to='/football' className="list-item-link" activeClassName="active">Football</NavLink>
            </li>
            <li className="list-item">
                <NavLink to='/basketball' className="list-item-link" activeClassName="active">Basketball</NavLink>
            </li>
            <li className="list-item">
                <NavLink to='/formula' className="list-item-link" activeClassName="active">Formula</NavLink>
            </li>
            <li className="list-item">
                <NavLink to='/fighting' className="list-item-link" activeClassName="active">Fighting</NavLink>
            </li>
        </ul>
    )
}

const myStyle = {
    background: '#e1b12c',
    color: '#f5f6fa'
}

export default NavbarLinks
